import Judge from "../js/judge";
import Report from "../js/report";
//
class StatusBlock{
    constructor(applicator,tactics,type,duration,waitTime){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.type = type;
        //
        this.duration = duration;
        //
        this.isBenefit = false;
        //
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        //
        this.beExecuted =  null;
    }
    //
    Init(target,needReport = true,reportLevel = 3){
        let j = new Judge();
        if(this.type == "" && j.IsGetStatus(target,'')
        || this.type == "" && j.IsGetStatus(target,'')
        || this.type == "" && j.IsGetStatus(target,'')){
            let report = new Report();
            report.InitExecuteReport(3,target,`${this.type}`);
            return false;
        }
        this.beExecuted = target;
        //target
        switch(this.type){
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
            case "" : this.isBenefit = true; break;
        }
        //
        for(let i in target.STATUS){
            //
            if(target.STATUS[i].type == this.type && this.type != '' && this.type != ''){
                let report = new Report();
                report.InitExecuteReport(3,target,`${target.STATUS[i].type}`);

                return false;
            }
        }
        target.STATUS.push(this);
        if(needReport){
            let str = ``;
            if(this.type == '' || this.type == '' || this.type == '' || this.type == '')str = `${this.type}`;
            else if(this.type == "")    str = ``;
            else if(this.type == "" || this.type == '' || this.type == '' || this.type == "")    str = `${this.type}`;
            else if(this.type == "" || this.type == "")    str = `${this.type}`;
            else if(this.type == '' || this.type == '') str = `${this.type}`
            else if(this.type == '' || this.type == '') str = `${this.type}`;
            let report = new Report();
            report.InitExecuteReport(reportLevel,target,str);
        }
        //
        for(let i in target.FALL_INTO_STATUS){
            //
            target.FALL_INTO_STATUS[i].GetData(this.applicator,this.tactics,this.type,this.duration,this.isBenefit,this.waitTime,this);
            //
            target.FALL_INTO_STATUS[i].Execute();
        }
        return true;
    }
    SelfDestroy(){
        let temp = []
        for(let i in this.beExecuted.STATUS){
            if(this.beExecuted.STATUS[i] != this) temp.push(this.beExecuted.STATUS[i]);
        }
        this.beExecuted.STATUS = temp;
    }
}

export default StatusBlock;